import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Component, NgZone, ViewChild } from '@angular/core';
import { Platform } from 'ionic-angular';
import { ElementRef } from '@angular/core';

declare var ApiAIPromises: any;
@IonicPage()
@Component({
  selector: 'page-messages',
  templateUrl: 'messages.html',
})
export class MessagesPage {
  chat = [];
  @ViewChild('myform') myform: ElementRef;

  ionViewDidEnter(){
    ApiAIPromises.requestText({
      query: "Hello"
    })
    .then(({result: {fulfillment: {speech}}}) => {
       this.ngZone.run(()=> {
         this.chat.push(speech);
       });
    })
  }

  ask(question) {
    this.chat.push(question);
    this.myform.nativeElement.value='';
    ApiAIPromises.requestText({
      query: question
    })
    .then(({result: {fulfillment: {speech}}}) => {
       this.ngZone.run(()=> {
         this.chat.push(speech);
       });
    })
  }

  constructor(public navCtrl: NavController, public navParams: NavParams, public platform: Platform, public ngZone: NgZone) { 
    platform.ready().then(() => {
      ApiAIPromises.init({
        clientAccessToken: "c5a0fc4423e64f63a6b29dac96eaf5ad"
      }).then(result => console.log(result));
    });
  }

}




